<template>
  <div>
    <v-card class="mx-auto" max-width="800" flat>
      <v-card-title>Your Referral Link</v-card-title>
      <v-card-text
        >Your referrals will earn an extra 10% minted CSE tokens on their Auction
        Lobby Purchase. As a referrer you will earn an extra 10%. There are no
        limits on number nor the amount of referrals that you can
        get.</v-card-text
      >
      <v-row class="ml-4 mr-4">
        <v-text-field
          id="referralInput"
          outlined
          class="px-4"
          label="Referral Link"
          readonly
          :value="referral"
          required
        ></v-text-field>
        <v-btn @click="copyLink()" block class="mt-1 mb-6 button"
          >Copy Link</v-btn
        >
      </v-row>
    </v-card>
  </div>
</template>

<script>
import Web3 from "web3";

export default {
  data() {
    return {
      address: "",
      referral: `${window.location.origin}?ref=${this.address}`,
    };
  },
  mounted() {
    console.log("created in app");
    /* eslint-disable no-unused-vars */
    const loginPromise = new Promise((resolve, reject) => {
      if (window.ethereum) {
        // Load WEB3
        if (typeof window.web3 !== "undefined") {
          window.web3 = new Web3(window.web3.currentProvider);
          console.log("connection");
          // Or connect to a node
        } else {
          window.web3 = new Web3(
            new Web3.providers.HttpProvider("http://localhost:3030")
          );
        }

        window.ethereum.enable();
        resolve(true);
      } else {
        window.addEventListener("load", () => {
          let poller = setInterval(() => {
            if (window.ethereum) {
              // Load WEB3
              if (typeof window.web3 !== "undefined") {
                window.web3 = new Web3(window.web3.currentProvider);
                console.log("connected");
                // Or connect to a node
              } else {
                window.web3 = new Web3(
                  new Web3.providers.HttpProvider("http://localhost:3030")
                );
              }

              window.ethereum.enable();
              resolve(true);
            }
            clearInterval(poller);
          }, 200);
          setTimeout(() => {
            clearInterval(poller);
          }, 10000);
        });
      }
    })
      .then(() => {
        console.log("Web3 installed and logged in");
        return true;
      })
      .catch((e) => {
        console.error("Error while detecting Web3", e);
        return false;
      });

    loginPromise.then(() => {
      /* eslint-disable-next-line no-async-promise-executor */
      return new Promise(async (resolve, reject) => {
        const accounts = await window.web3.eth.getAccounts();
        const userAddress = accounts[0];
        if (!userAddress) {
          return resolve(false);
        }
        this.address = userAddress;
        this.referral = `${window.location.origin}?ref=${this.address}`;

        setInterval(async () => {
          if (window.web3 && this.address !== userAddress)
            location.reload();
        }, 700);
      });
    });
  },
  methods: {
    copyLink() {
      const inputField = document.getElementById("referralInput");
      inputField.focus();
      inputField.select();
      inputField.setSelectionRange(0, 999999); //for mobile devices
      document.execCommand("copy");
    },
  },
};
</script>
