          outputs: [
            {
              name: "rawAmount",
              type: "uint256",
            },
            {
              name: "referrerAddr",
              type: "address",
            },
          ],
          payable: false,
          stateMutability: "view",
          type: "function",
        },
        {
          constant: true,
          inputs: [
            {
              name: "",
              type: "uint256",
            },
            {
              name: "",
              type: "address",
            },
          ],
          name: "xfLobbyMembers",
          outputs: [
            {
              name: "headIndex",
              type: "uint40",
            },
            {
              name: "tailIndex",
              type: "uint40",
            },
          ],
          payable: false,
          stateMutability: "view",
          type: "function",
        },
        {
          constant: true,
          inputs: [
            {
              name: "memberAddr",
              type: "address",
            },
          ],
          name: "xfLobbyPendingDays",
          outputs: [
            {
              name: "words",
              type: "uint256[2]",
            },
          ],
          payable: false,
          stateMutability: "view",
          type: "function",
        },
        {
          constant: true,
          inputs: [
            {
              name: "beginDay",
              type: "uint256",
            },
            {
              name: "endDay",
              type: "uint256",
            },
          ],
          name: "xfLobbyRange",
          outputs: [
            {
              name: "list",
              type: "uint256[]",
            },
          ],
          payable: false,
          stateMutability: "view",
          type: "function",
        },
      ];
      if (!contractAddress && !address) {
        return null;
      }
      this.mainContract = new window.web3.eth.Contract(abi, contractAddress);
      // this.mainContract.methods.stakeLists().call({
      //   address: this.user.address,
      // });
      this.contractLoaded();
    },
    contractLoaded() {
      if (!this.user.address) {
        return;
      }
      this.getUserBalance();
      setInterval(() => {
        this.getUserBalance();
      }, 1000 * 6);
      this.getCurrentDay();
      const intso = setInterval(() => {
        if (this.currentDay) {
          clearInterval(intso);
          // console.log('run_Stake==>>>', typeof run_Stake, 'run_Auction==>>', typeofrun_Auction, 'run_Dividends')
          if (typeof this.run_Stake === "function") {
            this.run_Stake();
          }
          if (typeof this.run_Auction === "function") {
            //run_Auction();
          }
          if (typeof this.run_Dividends === "function") {
            //run_Dividends();
          }
        }
      }, 100);
    },
    getUserBalance() {
      console.log(this.user);
      this.mainContract.methods
        .balanceOf(this.user.address)
        .call({
          shouldPollResponse: false,
        })
        .then((res) => {
          this.user.balance = res;
          this.yourTokenBalanceHd =
            "Your CSE balance: " +
            (this.user.balance / 100000000).toLocaleString();
        });
    },
    getCurrentDay() {
      this.mainContract.methods
        .globalInfo()
        .call({
          shouldPollResponse: true,
        })
        .then((res) => {
          this.currentDay = parseInt(res[4]);
        });

      setTimeout(() => {
        this.getCurrentDay();
      }, 1000 * 60 * 7);
    },
    run_Stake(nIrstRn) {
      console.log("in run_Stake==>>>");
      this.mainContract.methods
        .globalInfo()
        .call({
          shouldPollResponse: true,
        })
        .then((res) => {
          this.stakeData.stakingShare =
            window.ethereum.fromSun(res[2]["_hex"]) * this.SUN;
          this.stVal1 = this.currentDay + 1;

          this.stakeData.shareRate =
            (100000 / this.stakeData.stakingShare) * 100000000;
          this.stVal12 =
            this.abbreviate_number(this.stakeData.shareRate, 2) + "/CSE";
        });

      this.mainContract.methods
        .xfLobby(this.currentDay)
        .call({
          shouldPollResponse: true,
        })
        .then((res) => {
          this.stVal8 =
            this.abbreviate_number(window.ethereum.fromSun(res._hex), 2) +
            " TRX";
        });

      if (nIrstRn) return;

      this.getMyEndedStakes();
      // getDaysData();
      // estimateNextDay();
    },
    abbreviate_number(_num, fixed) {
      let num = parseFloat(_num);
      if (num === null) {
        return null;
      } // terminate early
      if (num === 0) {
        return "0";
      } // terminate early
      fixed = !fixed || fixed < 0 ? 0 : fixed; // number of decimal places to show
      var b = num.toPrecision(2).split("e"), // get power
        k = b.length === 1 ? 0 : Math.floor(Math.min(b[1].slice(1), 14) / 3), // floor at decimals, ceiling at trillions
        c =
          k < 1
            ? num.toFixed(0 + fixed)
            : (num / Math.pow(10, k * 3)).toFixed(1 + fixed), // divide by power
        d = c < 0 ? c : Math.abs(c), // enforce -0 is 0
        e = d + ["", "K", "M", "B", "T"][k]; // append power

      return e;
    },

    async getMyEndedStakes() {},
    renderMyEndedStakes(data) {},
  },
  mounted() {
    this.countdownTime =
      moment.utc().add(1, "day").startOf("day").tz("Etc/UTC").valueOf() -
      moment.utc().tz("Etc/UTC").valueOf();

    if (this.accessCookie("ref")["length"] > 0) {
      if (this.validateAddress(this.accessCookie("ref"))) {
        this.user.referrer = this.accessCookie("ref");
      }
    }
  },
  async created() {
    console.log("created in app");
    const loginPromise = new Promise((resolve, reject) => {
      if (window.ethereum) {
        // Load WEB3
        if (typeof window.web3 !== "undefined") {
          window.web3 = new Web3(window.web3.currentProvider);
          console.log("connection", window.web3);
          // Or connect to a node
        } else {
          window.web3 = new Web3(
            new Web3.providers.HttpProvider("http://localhost:3030")
          );
        }

        window.ethereum.enable();
        resolve(true);
      } else {
        window.addEventListener("load", () => {
          let poller = setInterval(() => {
            if (window.ethereum) {
              // Load WEB3
              if (typeof window.web3 !== "undefined") {
                window.web3 = new Web3(window.web3.currentProvider);
                console.log("connected");
                // Or connect to a node
              } else {
                window.web3 = new Web3(
                  new Web3.providers.HttpProvider("http://localhost:3030")
                );
              }

              window.ethereum.enable();
              resolve(true);
            }
            clearInterval(poller);
          }, 200);
          setTimeout(() => {
            clearInterval(poller);
          }, 10000);
        });
      }
    })
      .then(() => {
        console.log("Web3 installed and logged in");
        return true;
      })
      .catch((e) => {
        console.error("Error while detecting Web3", e);
        return false;
      });

    loginPromise.then(() => {
      // eslint-disable-next-line no-async-promise-executor
      return new Promise(async (resolve, reject) => {
        const accounts = await window.web3.eth.getAccounts();
        const userAddress = accounts[0];
        if (!userAddress) {
          return resolve(false);
        }
        this.user.address = userAddress;
        this.updateHeadAddress();
        this.setUpContracts();

        setInterval(async () => {
          if (window.web3 && this.user.address !== userAddress)
            location.reload();
        }, 700);
      });
    });
  },
};
</script>

