<template>
  <div>
    <div class="auction">
      <div>
        <v-card class="p-2">
          <v-card-title>Dividend Histogram</v-card-title>

          <area-chart
            :data="valuesObj"
            xtitle="Days"
            ytitle="Millions"
          ></area-chart>
        </v-card>
      </div>
      <div>
        <v-row class="mb-6" no-gutters>
          <v-dialog v-model="dialog" max-width="400">
            <v-system-bar color="#07d662" dark>
              <v-spacer></v-spacer>
              <v-icon>mdi-window-minimize</v-icon>
              <v-icon v-on:click="close()">mdi-close</v-icon>
            </v-system-bar>
            <v-card class="p-2">
              <v-text-field
                outlined
                class="px-4"
                label="Amount of ETH to Enter"
                required
                v-model="amountToStake"
              ></v-text-field>
              <v-btn
                class="button mb-2"
                block
                dark
                color="primary"
                v-on:click="enterLobbyFinal()"
                >Enter the Auction</v-btn
              >
            </v-card>
          </v-dialog>

          <v-col lg="4" md="4" sm="12" class="pa-2">
            <v-toolbar flat class="titlebar mt-5 pl-0">
              <v-toolbar-title>Current Dividends Pool</v-toolbar-title>
              <v-spacer></v-spacer>
              <p style="color: white !important"></p>
            </v-toolbar>

            <v-toolbar flat class="titlebar mt-5 pl-0">
              <v-spacer></v-spacer>
              <p style="color: white !important"></p>
            </v-toolbar>
          </v-col>

          <v-col lg="8" md="8" sm="12" class="pa-2">
            <v-skeleton-loader
              v-if="myObj.length < 0"
              class="mx-auto"
              type="table"
            ></v-skeleton-loader>

            <v-card v-if="myObj.length > 0" transition="fade-transition">
              <v-toolbar fixed class="dsp-titlebar mt-5 pl-0 pr-0">
                <v-toolbar-title class="ml-1 pl-0 pr-0 mr-0"
                  >Auction Lobby</v-toolbar-title
                >
                <v-spacer></v-spacer>
                <v-btn
                  class="mr-4 button-outline"
                  small
                  dark
                  v-on:click="enterLobby()"
                  >enter auction lobby</v-btn
                >
              </v-toolbar>
              <v-data-table
                :headers="headers"
                :items="myObj"
                :items-per-page="10"
                default-sort="day:desc"
                mobile-breakpoint="100"
                multi-sort
                class="elevation-1"
                loading-text="Loading... Please wait"
              >
                <template v-slot:item.status="{ item }">
                  <span v-if="item">
                    <v-btn
                      v-if="item.state == 'open'"
                      x-large
                      dark
                      v-on:click="enterLobby(item)"
                      min-width="100px"
                      >{{ auctionBtn }}</v-btn
                    >
                    <v-btn
                      v-else-if="item.state == 'closed' && item.entry > 0"
                      class="button shadow"
                      small
                      dark
                      v-on:click="collectLobby(item)"
                      min-width="100px"
                      >collect</v-btn
                    >
                    <v-btn
                      v-else-if="item.state == 'closed'"
                      small
                      color="#757575"
                      disabled
                      min-width="100px"
                      >ended</v-btn
                    >
                  </span>
                </template>
                <template v-slot:item.status="{ item }">
                  <span v-if="item">
                    <v-btn
                      v-if="item.state == 'open'"
                      class="button-outline"
                      small
                      dark
                      v-on:click="enterLobby(item)"
                      min-width="100px"
                      >Enter</v-btn
                    >
                    <v-btn
                      v-else-if="item.state == 'closed' && item.entry > 0"
                      class="button shadow"
                      small
                      dark
                      v-on:click="collectLobby(item)"
                      min-width="100px"
                      >collect</v-btn
                    >
                    <v-btn
                      v-else-if="item.state == 'closed'"
                      small
                      color="#757575"
                      disabled
                      min-width="100px"
                      >ended</v-btn
                    >
                  </span>
                </template>
                <template v-slot:item.received="{ item }">
                  <span v-if="item.received" style="text-align=center">
                    {{ item.received }}
                  </span>
                  <span v-else vstyle="text-align=center"> -- </span>
                </template>
                <template v-slot:item.entry="{ item }">
                  <span v-if="item.entry" style="text-align=center">
                    {{ item.entry }}
                  </span>
                  <span v-else style="text-align=center"> -- </span>
                </template>
              </v-data-table>
            </v-card>
          </v-col>
        </v-row>
      </div>

      <v-snackbar
        v-model="snackbar"
        timeout="10000"
        :color="color"
        top
        center
        >{{ text }}</v-snackbar
      >
    </div>
  </div>
</template>

