    async contractLoaded() {
      await this.getDivChartData();
      await this.getCurrentDay();
      await this.runDividends();
      setInterval(async () => {
        await this.getDivChartData();
        await this.getCurrentDay();
        await this.runDividends();
      }, 30000);

      try {
        await this.getLobbyTable();
      } catch (err) {
        console.log(err);
      }
